:root {
  --clr-primary: hsl(322, 100%, 66%);
  --clr-light: hsl(193, 100%, 96%);
  --clr-black: hsl(192, 100%, 9%);
  --clr-gray: hsl(208, 11%, 55%);
  --clr-white: #fff;
  --ff-sans-body: "Open Sans", sans-serif;
  --ff-sans-heading: "Poppins", sans-serif;
  --fs-h1: 1.5rem;
  --fs-h2: 1.25rem;
  --fs-body: 0.875rem;
  --fs-medium: 0.75rem;
  --fs-small: 0.695625rem;
  --fs-xsmall: 0.625rem;
  --transition: all 0.2s ease-in-out;
  /* 
    MOBILE
    heading: 24px - Poppins
    hero-text: 16px - Open Sans
    CTA-button: 12px - Open Sans
    sub-heading: 20px - Poppins
    body-text: 14px - Open Sans
    footer-heading: 18px - Poppins
    CTA-button-footer: 10px - Open Sans
    footer copyright: 11.13px - Open Sans

    DESKTOP
    heading: 40px - Poppins
    hero-text: 18px - Open Sans
    CTA-button: 14px - Open Sans
    sub-heading: 28px - Poppins
    body-text: 18px - Open Sans
    footer-heading: 32px - Poppins
    CTA-button-footer: 20px - Open Sans
    footer text: 14px - Open Sans
    footer copyright: 12px - Open Sans
  */
}
@media (min-width: 64em) {
  :root {
    --fs-h1: 2.5rem;
    --fs-h2: 1.75rem;
    --fs-body: 1.125rem;
  }
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-style: normal;
  font-weight: normal;
}

body {
  background-color: var(--clr-white);
  color: var(--clr-black);
}

html,
body {
  margin: 0 auto;
  width: 100%;
  box-sizing: border-box;
}

main {
  padding: 3.75rem 1.25rem;
}

.sr-only {
  clip: rec(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  width: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
}

.img-fluid {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  font-family: var(--ff-sans-body);
  font-weight: 400;
  font-size: var(--fs-body);
}

h1, h2 {
  font-weight: 600;
}

h1 {
  margin: 0 0 1.5rem;
  font-size: var(--fs-h1);
  line-height: 2.25rem;
}

h2 {
  margin: 0 0 1rem;
  font-size: var(--fs-h2);
}

.btn {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: none;
  background-image: none;
  text-decoration: none;
  font-size: inherit;
  transition: var(--transition);
  border-radius: 1.25rem;
}
@media (min-width: 64em) {
  .btn {
    border-radius: 2.5rem;
  }
}
.btn-primary {
  width: 15rem;
  height: 2.5rem;
  background-color: var(--clr-primary);
  color: var(--clr-white);
  font-size: var(--fs-medium);
  margin: 2.1875rem auto 3.4375rem;
}
@media (min-width: 64em) {
  .btn-primary {
    width: 17.5rem;
    height: 3.5rem;
    font-size: 0.875rem;
  }
}
.btn-secondary {
  height: 1.5rem;
  width: 6rem;
  background-color: var(--clr-white);
  color: var(--clr-black);
  font-size: var(--fs-xsmall);
  font-weight: 700;
  box-shadow: 0 0 0.1875rem 0.1875rem rgba(0, 0, 0, 0.06);
}
@media (min-width: 64em) {
  .btn-secondary {
    height: 3rem;
    width: 12.5rem;
    font-size: 0.875rem;
    box-shadow: 0 0 0.3125rem 0.3125rem rgba(0, 0, 0, 0.06);
  }
}

.card {
  box-shadow: 0 0 0.3125rem 0.3125rem rgba(0, 0, 0, 0.06);
  border-radius: 1rem;
  margin-bottom: 2.5rem;
  padding: 1.5rem 2.5rem;
}
.card__img {
  margin-bottom: 1.5rem;
}
.card__body {
  text-align: center;
  line-height: 1.3125rem;
}

.header {
  position: relative;
  background-image: url(../../../images/bg-hero-mobile.svg);
  background-color: var(--clr-light);
  background-size: cover;
  background-position: top center;
}

.navbar {
  display: flex;
  justify-content: space-between;
  padding: 1.5rem 1rem 0;
}
.navbar .logo {
  width: 6.5rem;
}
@media (min-width: 64em) {
  .navbar .logo {
    width: 12.5rem;
  }
}

.hero {
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 5rem 2.5rem 2.5rem;
}
.hero p {
  margin: 0;
  padding: 0;
  line-height: 1.5rem;
  font-size: 1rem;
}
@media (min-width: 64em) {
  .hero {
    flex-direction: row;
    align-items: center;
  }
  .hero__img {
    max-width: 42.5rem;
  }
}